import React from 'react';
import img1 from './images/prisonnier.jpg'



class Rules extends React.Component 
{
  constructor(props)
  {
    super(props);
    this.state = { 
      
    }
  }

  render()
  {
    return (
      <div className="App">
        <div className="pagesContent">

          <div className="goHomeContainer">
            <button className="bouton" onClick={this.props.fonction}>Homepage</button>
          </div>

            

          <div className="parallele">

            <div className="Colone">
              <p className="rulesParag">The Wall Street Journey is a turn-based strategy game. The player has to take three decisions per turn and analyze the results to learn how to make right decisions.
                The player should follow his intuitions and has to trust financial products and investment advisors with a very limited description of each opportunity. 
                Depending on your choices and available balance you will get a different Ending.</p>

                <p className="rulesParag">The Player is free to play with leverage, it means it can invest more money that he has in its total balance. note that this increases the risks to loose everything and to loose the game before the final turn</p>

                <p className="rulesParag"> 
                The playing interface is composed of two main parts : the daily newspaper and the investment board. The results of the previous decisions are displayed on the investment board and the player is free to start by reading its results, or the daily news.
                The investment board will show the available balance, and the evolution of the player’s assets. This evolution can be monitored with a graphic.
                The daily newspaper reports 3 news by category and with a different risk rate. Each news is linked to a choice, that has to be made on the board.. The player may decide to skip or to invest on the three decisions simultaneously, Or to go on one or two decisions over three. 
                When the player decides to take high risks, he will access to some news that are not available in safer scenarios. Such as the possibility of investing in scams, to ignore the court, believe gossips, pay for a lawyer… 
                A lot of news will be references to famous people in the investment ecosystem of New York City such has Warren Buffet, Bernard Madoff, Jordan Belfort. Some news about Initial Public Offerings (IPO) will also refer to tech giants and technologies we use every day. They’re going to be under fake names so the challenge is to recognize the great success opportunities that real-life investors have known in the past.
                </p>
            </div>
            <div className="Colone"><img className="prisonerImg" src={img1}></img></div>         
          </div>
        </div>  
      </div>
    );
  }
}

export default Rules;