import React from 'react';

import './App.css';
import Homepage from './components/Homepage';
import Rules from './components/Rules';
import Credits from './components/Credits';
import Play from './components/Play';
import Ending from './components/Ending';


class App extends React.Component 
{
  constructor(props)
  {
    super(props);
    this.state = { 

      Homepage : true,
      Rules : false,
      Credits : false,
      Play : false,
      
    }
  }


  goToRules=()=>
  {
    this.setState({
      Homepage : false,
      Rules : true,
      Credits : false,
      Play : false,
    });
    
  }

  goToPlay=()=>
  {
    this.setState({
      Homepage : false,
      Rules : false,
      Credits : false,
      Play : true,
    });
    
  }

  goToCredits=()=>
  {
    this.setState({
      Homepage : false,
      Rules : false,
      Credits : true,
      Play : false,
    });
    
  }


  goToHomePage=()=>
  {
    this.setState({
      Homepage : true,
      Rules : false,
      Credits : false,
      Play : false,
    });
    
  }


  



  render()
  {
    return (
      <div className="App">


          {this.state.Homepage && (
            <Homepage fonction={this.goToRules} fonction2={this.goToPlay} fonction3={this.goToCredits}/>
          )}
          {this.state.Rules && (
            <Rules fonction={this.goToHomePage}/>

          )}
          {this.state.Credits && (
            <Credits fonction={this.goToHomePage}/>
          )}
          {this.state.Play && (
            <Play fonction={this.goToHomePage}/>
          )}

          
      </div>
    );
  }
}

export default App;