        paragEnd="Congrats, you have unlocked an alternative ending to the story. It means that you had Mr Goodman as a Lawyer. As he couldn't protect you from issues with justice, his solution is to take you to a new life. Welcome to panama, here your illegal investment aren't considered as illegal.. Welcome to paradise, and enjoy your retirement";
